import * as axios from "axios";

//instance -> getUser
const instance = axios.create({
    withCredentials: true,
    baseURL: 'https://social-network.samuraijs.com/api/1.0/',
    headers: {"API-KEY": '619734cc-ca7d-4fb8-901a-2cfd8eb83c65'}
});


//Страница профиля
export const profileAPI = {
    getProfile(userId) {
        return instance.get(`profile/` + userId);
    },
    getStatus(userId) {
        return instance.get(`profile/status/` + userId);
    },
    updateStatus(status) {
        return instance.put(`profile/status`, {status: status});
    },
    savePhoto(photoFile) {
        const formData = new FormData();
        formData.append("image", photoFile);
        return instance.put(`profile/photo`, formData, {
            headers:{
                'Content-Type':'multipart/form-data'
            }
        });
    },
    saveProfile(profile){
        return instance.put(`profile`, profile);
    },
}


//Страница пользователей
export const usersAPI = {
    getUsers(currentPage = 1, pageSize = 10) {
        return instance.get(`users?page=${currentPage}&count=${pageSize}`)
            .then(response => {
                return response.data;
            });
    },
    getProfile(userId) {
        console.warn("Obsolete method. Please use profile API object.")
        return profileAPI.getProfile(userId);
    },

    follow(userId) {
        return instance.post(`follow/${userId}`)
    },
    unfollow(userId) {
        return instance.delete(`follow/${userId}`)
    }
}

// Авторизован ли я?
export const authAPI = {
    me() {
        return instance.get(`auth/me`);
    },
    login(email, password, rememberMe=false, captcha=null) {
        return instance.post(`auth/login`, {email, password, rememberMe,captcha});
    },
    logout() {
        return instance.delete(`auth/login`);
    }
}

//Captcha
// API-> AUTH REDUCER - Thunk const getCaptchaUrl & set to
// local state- set Action Creator ->
// В санке Login высываем(просим, диспатчим) при ошибке "10" getCaptchaUrl
// После запроса captcha URL записывается в local state. Когда урл
// присутствует, мы покажем картинку каптчи на странице
// логинизации Login-> forms-redux. Далее Login->mapStateToProps. ->
// прокидываем пропсы в forms-redux Login form
// Auth-reducer case. -> сабмитим логин в auth reducer login-> api login

export const securityAPI = {
    getCaptchaUrl() {
        return instance.get(`security/get-captcha-url`);
    }
}

